const ButtonWrapper = styled.div`
  display: flex;
  justify-content: center;
  gap: 20px;
  align-items: center;
  width: 100%;
  padding: 20px 0;
`;

const Button = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  width: 48%;
  border: 2px solid #000000;
  background-color: #ffffff;
  padding: 12px 20px;
  border-radius: 10px;
  color: #000000;
  transition: 0.2s;
`;

const SubmitBtn = styled(Button)`
  background-color: #000000;
  color: #ffffff;
`;

const Main2 = () => {
  const navigate = useNavigate();
  const workflow = window.location.pathname.split("/").pop();
  const serverAddress = process.env.REACT_APP_SERVER_ADDRESS;

  const fileInputRefs = useRef({});

  const [clientId, setClientId] = useState(0);

  const [isLoading, setIsLoading] = useState(false);

  const [progress, setProgress] = useState(0);

  const [selectedFiles, setSelectedFiles] = useState([]);

  const [request, setRequest] = useState({});

  const [imageSrc, setImageSrc] = useState("");

  const [currentSlide, setCurrentSlide] = useState(0); // 현재 슬라이더 페이지(인덱스) 상태

  const sliderRef = useRef();

  const [prompt, setPrompt] = useState("");

  const [isError, setIsError] = useState(false);

  useEffect(() => {
    console.log("prompt : ", prompt);
  }, [prompt]);

  const triggerFileInput = (key) => {
    // 해당 key의 파일 입력 창 열기
    fileInputRefs.current[key].click();
  };

  const clickSubmit = async () => {
    if (clientId === 0) return;
    let flag = false;
    Object.keys(request).forEach((key) => {
      if (request[key].default === "") {
        Swal.fire({
          icon: "error",
          title: "입력값을 모두 채워주세요.",
        });
        flag = true;
        return;
      }
    });
    if (flag) return;
    handleNext();

    // 웹 소켓 연결 시작
    const ws = new WebSocket(`ws://${serverAddress}/ws?clientId=${clientId}`);
    ws.onopen = async () => {
      console.log("Connected to WebSocket server");

      // 모든 이미지에 대해 handleUpload 실행 및 gun 변수에 저장
      await handleUpload(clientId);

      // 기존 clickSubmit 기능 실행
      const data = {
        workflow: `${workflow}`,
      };
      Object.keys(request).forEach((key) => {
        data[key] = request[key].default;
      });

      await sendRequest(clientId, data);
    };

    ws.onmessage = async (event) => {
      const jsonRes = JSON.parse(event.data);
      console.log("jsonRes : ", jsonRes);

      if (jsonRes.status === "progress") {
        setIsLoading(true);
        setProgress(jsonRes.detail);
        setPrompt(jsonRes.status);
      }
      if (jsonRes.status === "error") {
        setPrompt(jsonRes.status);
        setIsError(true);
      }
      if (jsonRes.detail === "Execution is done") {
        await getHistory(clientId);
      }
      if (jsonRes.status === "closed") {
        setIsLoading(false);
        ws.close();
      }
    };

    ws.onclose = () => {
      console.log("Disconnected from WebSocket server");
    };

    ws.onerror = (error) => {
      console.error("WebSocket error:", error);
    };
  };

  const handleFileChange = (event, key) => {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onloadend = (e) => {
      const arrayBuffer = reader.result;
      const blob = new Blob([arrayBuffer], { type: file.type });
      const imageUrl = URL.createObjectURL(blob);
      // 기존에 key 가 있는 파일이 있다면 새로운 이미지로 대체하도록
      const idx = selectedFiles.findIndex((item) => item.key === key);
      if (idx !== -1) {
        const newSelectedFiles = [...selectedFiles];
        newSelectedFiles[idx] = { key, file, preview: imageUrl };
        setSelectedFiles(newSelectedFiles);
        return;
      }
      // 기존에 key 가 없는 파일이라면 새로 추가
      else {
        setSelectedFiles((prevSelectedFiles) => [
          ...prevSelectedFiles,
          { key, file, preview: imageUrl },
        ]);
      }
      const arr = new Uint8Array(reader.result).subarray(0, 4);
      let header = "";
      for (let i = 0; i < arr.length; i++) {
        header += arr[i].toString(16);
      }
    };
    reader.readAsArrayBuffer(file);
  };
  const handleUpload = async (clientId) => {
    const formData = new FormData();

    // 여러 파일과 데이터를 FormData에 추가
    for (const { key, file } of selectedFiles) {
      formData.append(key, file, file.name);
    }

    // API 엔드포인트로 POST 요청 보내기
    try {
      const response = await fetch(
        `http://${serverAddress}/upload?clientId=${clientId}`,
        {
          method: "POST",
          body: formData,
        },
      );

      const data = await response.json(); // JSON 응답을 가정

      console.log("Upload successful:", data);

      if (response.ok) {
        // 서버로부터의 응답 처리
        Object.keys(data).forEach((key) => {
          console.log("key : ", key);
          console.log("data[key] : ", data[key]);
          const newRequest = { ...request };
          newRequest[key].default = data[key];
          setRequest(newRequest);
        });
      } else {
        console.error("Upload failed:", response.statusText);
      }
    } catch (error) {
      console.error("Error uploading file:", error);
    }
  };

  const handleValueChange = (index, value) => {
    const newRequest = { ...request };
    newRequest[index].default = value;
    setRequest(newRequest);
  };

  async function getHistory(clientId, download = true) {
    console.log("getHistory 실행");
    try {
      const response = await fetch(
        `http://${serverAddress}/history?clientId=${clientId}`,
      );
      console.log("getHistory response : ", response);

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      if (download) {
        await parseMultipartResponse(response);
      }

      return response;
    } catch (e) {
      console.error("Request failed:", e.message);
    }
  }

  async function parseMultipartResponse(response) {
    const contentType = response.headers.get("Content-Type");
    const boundary = contentType.split("boundary=")[1].trim();
    const arrayBuffer = await response.arrayBuffer();

    const textDecoder = new TextDecoder("utf-8");
    const text = textDecoder.decode(arrayBuffer);

    const parts = text
      .split(`--${boundary}`)
      .filter((part) => part.trim() !== "--" && part.trim() !== "");

    for (let part of parts) {
      const [headerPart, bodyPart] = part.split("\r\n\r\n", 2);
      const headers = headerPart.split("\r\n");
      let contentDisposition = headers.find((header) =>
        header.startsWith("Content-Disposition"),
      );

      if (contentDisposition) {
        const filenameMatch = contentDisposition.match(/filename="(.+)"/);
        if (filenameMatch) {
          const filename = filenameMatch[1];
          const contentTypeHeader = headers.find((header) =>
            header.startsWith("Content-Type"),
          );
          const contentType = contentTypeHeader.split(": ")[1];

          // 시작점 찾기
          const bodyStart = text.indexOf(bodyPart);
          const bodyEnd = bodyStart + bodyPart.trim().length * 2;
          const bodyArrayBuffer = arrayBuffer.slice(bodyStart, bodyEnd);

          const blob = new Blob([bodyArrayBuffer], { type: contentType });
          const url = URL.createObjectURL(blob);

          setImageSrc(url); // 이미지 URL 설정 함수
        }
      }
    }
  }

  const toDataURL = (url) => {
    return fetch(url)
      .then((response) => {
        return response.blob();
      })
      .then((blob) => {
        return URL.createObjectURL(blob);
      });
  };

  function downloadImageWithCanvas(imageUrl) {
    var img = new Image();
    img.crossOrigin = "Anonymous"; // 필요한 경우 CORS 설정

    img.onload = function () {
      var canvas = document.createElement("canvas");
      var ctx = canvas.getContext("2d");

      // 고해상도 화면에서 적절한 캔버스 크기 설정
      var ratio = window.devicePixelRatio || 1;
      var width = img.naturalWidth * ratio;
      var height = img.naturalHeight * ratio;

      // 캔버스 크기 조절
      canvas.width = width;
      canvas.height = height;
      ctx.scale(ratio, ratio); // 화면의 해상도에 맞게 조절

      // 이미지를 캔버스에 그림
      ctx.drawImage(img, 0, 0, img.naturalWidth, img.naturalHeight);

      // 캔버스의 내용을 이미지로 변환하고 다운로드
      canvas.toBlob(function (blob) {
        var url = URL.createObjectURL(blob);
        var a = document.createElement("a");
        a.href = url;
        a.download = "downloaded_image.png";
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      }, "image/png");
    };
    img.src = imageUrl;
  }

  async function sendRequest(clientId, data) {
    const headers = { "Content-Type": "application/json" };
    console.log("sendRequest data : ", data);
    try {
      const response = await fetch(
        `http://${serverAddress}/generate-based-workflow?clientId=${clientId}`,
        {
          method: "POST",
          headers: headers,
          body: JSON.stringify(data),
        },
      );
      console.log("sendRequest response : ", response);

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      // 스트림 리더를 통해 데이터를 점진적으로 읽습니다.
      const reader = response.body.getReader();
      while (true) {
        const { done, value } = await reader.read();
        if (done) {
          console.log("Stream completed");
          break;
        }
        // TextDecoder를 사용하여 Uint8Array를 문자열로 변환
        console.log("Received chunk", new TextDecoder().decode(value));
      }
      return await response.json(); // 이 부분은 사용하지 않음
    } catch (e) {
      console.error("Request failed:", e.message);
    }
  }

  async function getWorkflowInfo(workflow) {
    const headers = { "Content-Type": "application/json" };

    try {
      const response = await fetch(
        `http://${serverAddress}/workflow-info?workflow=${workflow}`,
        {
          method: "GET",
          // headers: headers,
          // body: JSON.stringify({ workflow: workflow }),
        },
      );

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      return await response.json(); // 응답 데이터를 JSON으로 파싱
    } catch (e) {
      console.error("Request failed:", e);
      return null; // 오류 발생 시 null 반환 또는 적절한 오류 처리
    }
  }

  const extractImgType = (value) => {
    return (
      value.split(".")[1] === "jpg" ||
      value.split(".")[1] === "png" ||
      value.split(".")[1] === "jpeg"
    );
  };

  useEffect(() => {
    const randomClientId = uuidv4();
    setClientId(randomClientId);

    getWorkflowInfo(`${workflow}`)
      .then((info) => {
        Object.keys(info).forEach((key) => {
          if (info[key].type === "str" && extractImgType(info[key].default)) {
            // image 로 판별되면 isImage: true 추가
            info[key].isImage = true;
          }
        });
        setRequest(info);
      })
      .catch((e) => {
        console.error("Error getting workflow info:", e);
      });
  }, []);
  const settings = {
    infinite: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    draggable: false,
    beforeChange: (current, next) => setCurrentSlide(next), // 다음 슬라이드 인덱스 업데이트
    swipe: false,
    speed: 200, // 넘어가는 시간
  };
  const handlePrev = () => {
    sliderRef.current.slickPrev();
  };

  const handleNext = () => {
    if (currentSlide < Object.keys(request).length) {
      console.log(
        "Object.keys(request).length : ",
        Object.keys(request).length,
      );
      console.log("currentSlide : ", currentSlide);
      sliderRef.current.slickNext();
    }
  };

  return (
    <MainContainer>
      <MainHeader>
        <BackBtn
          onClick={() => {
            navigate("/");
          }}
        />
        {workflow}
      </MainHeader>
      <MainWrapper>
        <Slider {...settings}>
          <TestImg>Test Image</TestImg>
          <TestImg>Test Image</TestImg>
          <TestImg>Test Image</TestImg>
          <TestImg>Test Image</TestImg>
          <TestImg>Test Image</TestImg>
          <TestImg>Test Image</TestImg>
        </Slider>
        <InputWrapper>
          {currentSlide !== Object.keys(request).length && (
            <ButtonWrapper>
              <Button onClick={handlePrev}>이전</Button>
              {currentSlide === Object.keys(request).length - 1 ? (
                <SubmitBtn
                  onClick={() => {
                    clickSubmit();
                  }}
                >
                  선택완료
                </SubmitBtn>
              ) : (
                <Button onClick={handleNext}>다음</Button>
              )}
            </ButtonWrapper>
          )}
          <Slider {...settings} ref={sliderRef}>
            {Object.entries(request).map(([key, value], index) => {
              if (value.type === "str" && !value.isImage) {
                return (
                  <InputItem>
                    <InputTitle>{value.title}</InputTitle>
                    <Textarea
                      aria-label="minimum height"
                      minRows={3}
                      placeholder="Minimum 3 rows"
                      defaultValue={value.default}
                      onChange={(e) => handleValueChange(key, e.target.value)}
                    />
                  </InputItem>
                );
              }
              return null;
            })}
            {Object.entries(request).map(([key, value], index) => {
              if (value.type === "int") {
                return (
                  <InputItem>
                    <InputTitle>{value.title}</InputTitle>
                    <NumberTF
                      key={index}
                      type="number"
                      variant="outlined"
                      defaultValue={value.default}
                      onChange={(e) => {
                        handleValueChange(key, Number(e.target.value));
                      }}
                    />
                  </InputItem>
                );
              }
              return null;
            })}
            {Object.entries(request).map(([key, value], index) => {
              if (value.type === "float") {
                return (
                  <InputItem>
                    <InputTitle>{value.title}</InputTitle>
                    <NumberTF
                      key={index}
                      type="number"
                      variant="outlined"
                      defaultValue={value.default}
                      inputProps={{ step: 0.01 }}
                      onChange={(e) => {
                        handleValueChange(key, Number(e.target.value));
                      }}
                    />
                  </InputItem>
                );
              }
              return null;
            })}
            {Object.entries(request).map(([key, value], index) => {
              if (value.type === "str" && value.isImage) {
                return (
                  <InputItem key={key}>
                    <InputTitle>{value.title}</InputTitle>
                    <UploadInput
                      type="file"
                      accept="image/*"
                      onChange={(e) => handleFileChange(e, key)}
                      ref={(el) => (fileInputRefs.current[key] = el)}
                    />
                    {selectedFiles.find((item) => item.key === key) ? (
                      <SubmitImgWrapper>
                        <PreviewImg
                          src={
                            selectedFiles.find((item) => item.key === key)
                              .preview
                          }
                          onClick={() => triggerFileInput(key)}
                          alt="Preview"
                        />
                      </SubmitImgWrapper>
                    ) : (
                      <SubmitImgWrapper>
                        <PreviewImg
                          src={"/images/image.svg"}
                          onClick={() => triggerFileInput(key)}
                        />
                      </SubmitImgWrapper>
                    )}
                  </InputItem>
                );
              }
              return null;
            })}
            <InputWrapper>
              {
                <ResultWrapper>
                  {isLoading && (
                    <LoadingWrapper>
                      <LoadingImg src={"/images/spinner.svg"} />
                      <LoadingText>{progress}</LoadingText>
                    </LoadingWrapper>
                  )}
                  {!isLoading && imageSrc && (
                    <DownloadWrapper>
                      <ImageDisplay
                        id="result-image"
                        src={imageSrc}
                        onClick={() => {
                          downloadImageWithCanvas(imageSrc);
                        }}
                      />
                      <DownText>이미지를 클릭하면 다운로드됩니다.</DownText>
                    </DownloadWrapper>
                  )}
                  {!isLoading && !imageSrc && !isError && (
                    <>
                      <WaitingText>이미지 생성 준비중입니다.</WaitingText>
                    </>
                  )}
                  {isError && (
                    <>
                      <WaitingText>잠시 후 다시 시도해주세요.</WaitingText>
                    </>
                  )}
                </ResultWrapper>
              }
            </InputWrapper>
          </Slider>
        </InputWrapper>
      </MainWrapper>
    </MainContainer>
  );
};

export default Main2;
